import { apiHelper } from '../utils/helpers'
const getToken = () => localStorage.getItem('token')

export default {
  getCart() {
    return apiHelper.get('/cart')
  },
  postCart() {
    return apiHelper.post('/cart')
  },
  addCartItem({ productId }) {
    return apiHelper.post(`/cartItem/${productId}/add`, null)
  },
  subCartItem({ productId }) {
    return apiHelper.post(`/cartItem/${productId}/sub`)
  },
  deleteCartItem({ productId }) {
    return apiHelper.delete(`/cartItem/${productId}`)
  },
  getOrders() {
    return apiHelper.get('/orders')
  },
  postOrder() {
    return apiHelper.post('/order')
  },
  cancelOrder({ orderId }) {
    return apiHelper.post(`/order/${orderId}/cancel`)
  },
  getPayment({ orderId }) {
    return apiHelper.get(`/order/${orderId}/payment`)
  },
  spgatewayCallback({}) {
    return apiHelper.post('/spgateway/callback')
  }
}
